.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: 0;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s cubic-bezier(.51, .92, .24, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__close {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.basicLightbox__placeholder {
  text-align: center;
  z-index: 1;
  will-change: transform;
  transition: transform .4s cubic-bezier(.51, .92, .24, 1), -webkit-transform .4s cubic-bezier(.51, .92, .24, 1);
  transform: scale(.9);
}

.basicLightbox__placeholder > img {
  max-height: 100%;
  max-width: 100%;
  width: auto;
  height: auto;
  margin: 0;
}

.basicLightbox__placeholder > iframe {
  max-width: 100%;
  margin: 0;
}

.basicLightbox__placeholder [data-basicLightbox] {
  display: block !important;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

[data-basicLightbox] {
  display: none !important;
}

